import React from 'react';
import { Button, Jumbotron, Nav } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

const HomeScreen = () => {
    return (
        <Jumbotron className="home-container">
            <p className="lead">Hi, my name is</p>          
            <h1 className="display-3">Richard Montgomery</h1>
            <hr className="my-4"></hr>
            <p className="home-body">
                I am a web developer living on the Kitsap Peninsula in Washington State.
                I enjoy building functional web applications using interactive, adaptive
                and responsive design.
            </p>
            <Nav>
                <LinkContainer to="/about">
                    <Nav.Link>
                        <Button className="btn btn-primary btn-lg" role="button">Let's Go</Button>
                    </Nav.Link>
                </LinkContainer>
            </Nav>
        </Jumbotron>
    )
}

export default HomeScreen;